

import { actions } from "./store"


const defaultStore = {
    count : 0,
}

export let counterReducer = (state = defaultStore, action) =>{

switch (action.type){

    case actions.increment:
        return {...state, count: state.count + 1}

    case actions.decrement:
        return {...state, count: state.count - 1}
    
    case actions.resetCounter:
        return{...state, count: 0}


    default:
        return state

}
}


export const decrease = (dispatch) =>{
    dispatch({type: actions.decrement})
}
export const increase = (dispatch) =>{
    dispatch({type: actions.increment})
}
export const resetCounter = (dispatch) =>{
    dispatch({type:actions.resetCounter})
}